export default "@import (reference) \"nui-framework-variables\";\n@import (reference) \"../../../../../../src/styles/components/combobox-v2-addons.less\";\n@import (reference) \"mixins\";\n\nnui-select-v2 {\n    width: 320px;\n    height: 36px;\n}\n\nnui-select-v2-option {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    &:not(.selected) {\n        .setCssVariable(color, primary_blue) !important;\n    }\n\n    &.selected {\n        .setCssVariable(background-color, nui-color-selected) !important;\n    }\n}\n\n.custom-container {\n    cursor: pointer;\n    display: flex;\n    width: 100%;\n    height: 100%;\n    align-items: center;\n    justify-content: space-between;\n    padding: @nui-space-xs @nui-space-sm;\n}\n\n.custom-input-template {\n    display: flex;\n    align-items: center;\n\n    .setCssVariable(color, primary_blue);\n    font-family: @font-family-open-sans;\n    font-size: @nui-font-size-hero;\n    font-weight: @nui-font-weight-regular;\n    line-height: @nui-line-height-big;\n\n    nui-icon {\n        position: relative;\n        top: -4px;\n    }\n}\n\n.custom-box {\n    .nui-combobox-v2__popup-box();\n    width: 100%;\n    max-width: unset !important;\n    justify-content: flex-start !important;\n}\n\n\n";