export default "@import (reference) \"nui-framework-variables\";\n@import (reference) \"mixins\";\n\n.picker-tile-size() {\n    width: 30px;\n    height: 30px;\n}\n\n.color-box() {\n    width: @nui-space-md + 1;\n    height: @nui-space-md + 1;\n    border-radius: @nui-radius-default;\n    border: 1px solid;\n    .setCssVariable(border-color, nui-color-line-default);\n}\n\n.color-box-input() {\n    .color-box();\n    width: @nui-space-md + @nui-space-xxs;\n    height: @nui-space-md + @nui-space-xxs;\n}\n\n.box {\n    .picker-tile-size()\n}\n\n.color-picker {\n    &-container {\n        padding: @nui-space-xs @nui-space-sm;\n    }\n    &-select {\n        width: fit-content;\n        min-width: unset;\n        cursor: pointer;\n    }\n\n    &-input {\n        display: flex;\n\n        &__box {\n            display: flex;\n            align-items: center;\n            flex-grow: 1;\n        }\n\n        &__color-box {\n            .color-box-input();\n            flex-grow: 1;\n            margin: 0 10px;\n        }\n\n        &__toggle {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            border-left: 1px solid;\n            .setCssVariable(border-left-color, nui-color-line-default);\n            .picker-tile-size();\n        }\n    }\n\n    &-option {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        height: 100%;\n\n        &__color-box {\n            .color-box();\n            padding: 1px 0 0 1px;\n        }\n\n        &--selected{\n            .setCssVariable(background-color, nui-color-selected);\n            &:focus {\n                outline: none;\n            }\n        }\n\n        &__glyph {\n            .nui-checkbox__glyph();\n            background-repeat: no-repeat;\n        }\n    }\n}\n";